$pc: 1200px; // PC
$tab: 1279px; // タブレット
$sp: 767px;  // スマホ

@mixin pc {
  @media (min-width: ($pc)) {
    @content;
  }
}
@mixin tab {
  @media (max-width: ($tab)) {
    @content;
  }
}
@mixin sp {
  @media (max-width: ($sp)) {
    @content;
  }
}


// カラー
$color-black: #101010;
$color-darkGray: #222222;
$color-gray: #414141;
$color-white: #fff;


@mixin font-xl {
  font-size: 3.2rem;
  font-weight: 600;
  line-height: 1.6;
  @include sp {
    font-size: 2.2rem;
    line-height: 1.4;
  }
}
@mixin font-l {
  font-size: 1.8rem;
  line-height: 2;
  @include sp {
    font-size: 1.4rem;
    line-height: 1.7;
  }
}
@mixin font-m {
  font-size: 1.6rem;
  line-height: 2;
  @include sp {
    font-size: 1.4rem;
    line-height: 1.7;
  }
}
@mixin font-s {
  font-size: 1.2rem;
  line-height: 1.9;
}