@use "../foundation/mixin";

// ヘッダー
.header {
  max-width: mixin.$pc;
  margin: 0 auto;
  padding: 15px 20px;
  display: flex;
  align-items: center;
  @include mixin.sp {
    padding: 12px 3%;
  }
}

// ロゴ
.logo {
  width: 140px;
  margin-right: auto;
  a {
    display: block;
  }
  &:hover {
    opacity: 0.7;
    transition: 0.1s;
  }
  @include mixin.tab {
    width: 120px;
  }
  @include mixin.sp {
    width: 90px;
  }
}

// ナビ
.navi-icon {
  margin-top: 6px;
  display: flex;
  align-items: center;
  .navi-icon__item {
    width: 22px;
    a {
      display: block;
      &:hover {
        opacity: 0.7;
        transition: 0.1s;
      }
    }
    img {
      width: 100%;
    }
    &:nth-child(n + 2) {
      margin-left: 25px;
    }
  }
  @include mixin.sp {
    margin-top: 0;
    .navi-icon__item {
      width: 20px;
    }
  }
}

// メインビジュアル
.main-visual {
  max-width: mixin.$pc;
  margin: 0 auto;
  padding: 0 20px;
  img {
    width: 100%;
  }
  @include mixin.sp {
    margin: 0;
    padding: 0;
  }
}

// セクション
.section {
  margin-top: 100px;
  .section__inner {
    max-width: mixin.$pc;
    padding: 0 20px;
    margin: 0 auto;
  }
  @include mixin.tab {
    margin-top: 70px;
  }
  @include mixin.sp {
    margin-top: 45px;
    .section__inner {
      padding: 0 3%;
    }
  }
}
.section--dark {
  background-color: mixin.$color-black;
  padding: 70px 0;
  .headingLv2 {
    color: mixin.$color-white;
    margin-top: 0;
  }
  @include mixin.tab {
    padding: 60px 0;
  }
  @include mixin.sp {
    padding: 40px 0 45px;
  }
}

// 見出し
.headingLv2 {
  @include mixin.font-xl;
  text-align: center;
}

// リードテキスト
.lead {
  @include mixin.font-l;
  text-align: center;
  margin-top: 40px;
  @include mixin.sp {
    margin-top: 25px;
  }
}

// オフィシャルTwitter
.official-twitter {
  display: flex;
  margin-top: 40px;
  justify-content: center;
  @include mixin.sp {
    margin-top: 20px;
  }
}

// ムービー
.movie {
  display: flex;
  margin-top: 40px;
  justify-content: center;
  &__item {
    width: calc(96% / 3);
    display: block;
    iframe {
      width: 100%;
    }
    &:nth-child(n + 2) {
      margin-left: 2%;
    }
  }
  @include mixin.tab {
    flex-wrap: wrap;
    justify-content: center;
    &__item {
      width: 100%;
      &:nth-child(n + 2) {
        margin-top: 20px;
        margin-left: 0;
      }
    }
  }
  @include mixin.sp {
    margin-top: 20px;
  }
}

// ミュージック
.music {
  display: flex;
  flex-wrap: wrap;
  margin-top: 40px;
  .music__item {
    display: flex;
    width: calc(90% / 6);
    &:nth-child(n + 2) {
      margin-left: 2%;
    }
    &:nth-child(7) {
      margin-left: 0;
    }
    &:nth-child(n + 7) {
      margin-top: 30px;
    }
    a {
      color: mixin.$color-white;
      text-align: center;
      @include mixin.font-l;
      display: flex;
      flex-direction: column;
      border-radius: 5px;
      padding: 20px;
      background-color: mixin.$color-darkGray;
      &:hover {
        background-color: mixin.$color-gray;
        transition: 0.1s;
      }
    }
    .music__logo {
      display: block;
      padding: 10px;
    }
    .music__logo img {
      width: 100%;
    }
  }
  @include mixin.tab {
    .music__item {
      width: calc(94% / 4);
      &:nth-child(n + 2) {
        margin-left: 2%;
      }
      &:nth-child(5),&:nth-child(9) {
        margin-left: 0;
      }
      &:nth-child(n + 5) {
        margin-top: 30px;
      }
    }
  }
  @include mixin.sp {
    margin-top: 20px;
    .music__item {
      width: calc(96% / 3);
      &:nth-child(n + 2) {
        margin-left: 2%;
      }
      &:nth-child(3n + 1) {
        margin-left: 0;
      }
      &:nth-child(n + 4) {
        margin-top: 10px;
      }
      a {
        padding: 10px;
      }
    }
  }
}

// SNS
.sns {
  display: flex;
  flex-wrap: wrap;
  margin-top: 40px;
  .sns__item {
    display: flex;
    width: calc(96% / 3);
    &:nth-child(n + 2) {
      margin-left: 2%;
    }
    a {
      display: flex;
      flex-direction: column;
      color: mixin.$color-white;
      text-align: center;
      @include mixin.font-m;
      border-radius: 5px;
      padding: 20px;
      border: 2px solid mixin.$color-black;
      width: 100%;
      color: mixin.$color-black;
      font-weight: bold;
      &:hover {
        opacity: 0.7;
        transition: 0.1s;
      }
    }
    .sns__icon {
      display: block;
      padding: 10px;
    }
    .sns__icon img {
      width: 55px;
    }
    .sns__text {
      margin-top: auto;
      display: block;
    }
  }
  @include mixin.tab {
    .sns__item {
      a {
        padding: 15px;
      }
      .sns__icon img {
        width: 50px;
      }
    }
  }
  @include mixin.sp {
    margin-top: 20px;
    .sns__item {
      a {
        padding: 10px;
      }
      .sns__icon img {
        width: 28px;
      }
    }
  }
}

// ストア
.store {
  margin-top: 40px;
  text-align: center;
  a {
    display: inline-block;
    border-radius: 5px;
    padding: 25px 60px 10px;
    border: 2px solid mixin.$color-black;
    text-align: center;
    &:hover {
      opacity: 0.7;
      transition: 0.1s;
    }
  }
  .store__image {
    max-width: 486px;
    margin: 0 auto;
    display: block;
  }
  .store__text {
    max-width: 120px;
    margin: 0 auto;
    display: block;
  }
  img {
    width: 100%;
  }
  @include mixin.tab {
    margin-top: 30px;
    .store__image {
      max-width: 500px;
    }
  }
  @include mixin.sp {
    margin-top: 20px;
    .store__text {
      max-width: 100px;
    }
    a {
      padding: 25px 30px 10px;
    }
  }
}

// フッター
.footer {
  border-top: mixin.$color-black 1px solid;
  padding: 20px;
  margin-top: 100px;
  .footer__text {
    text-align: center;
    @include mixin.font-s;
  }
  @include mixin.sp {
    margin-top: 50px;
    padding: 15px;
  }
}

// 汎用クラス
.pc-none {
  display: none;
  @include mixin.sp {
    display: block;
  }
}
.sp-none {
  display: block;
  @include mixin.sp {
    display: none;
  }
}